/* styles.css */
:root {
    --text-color: #383737;
    --link-color: #4a76ee;
    --background-color: #ffffff;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: var(--background-color);
}

/* Top Nav bar*/
nav {
    display: flex;  
    justify-content: space-between;
    background-color: rgb(91, 202, 202);
    padding: 0 60px;
    align-items: center;
    height: 100px;
}
nav .left a {
    color: var(--text-color);
    font-size: 33px;
    font-weight: 600;
}

nav .right a {
    color: var(--text-color);
    margin: 0 10px;
}

a {
    text-decoration: none;
    color: var(--link-color);
    font-weight: bold;
}

/* Personal Information area*/

.personalinfo .text {
    justify-content: center;
    display: flex;
}
.personalinfo .text h2{
    font-size: 40px;
}
.personalinfo .paragraph {
    margin: 30px 450px;
    font-size: 22px;
    justify-content: center;
    display: flex;
    text-align: center;
}
.personalinfo .para2 {
    margin: 20px 450px;
    font-size: 22px;
    justify-content: center;
    display: center;
    text-align: center;
}
.personalinfo .picture {
    justify-content: center;
    display: flex;
    margin-top: 50px;
}
.personalinfo .picture img {
    width: 450px;
    height: 500px;
    border-radius: 50%;
    justify-content: center;
    display: flex;
}
.personalinfo {
    justify-content: center;
    display: center;
    padding: 0 50px;
    margin: 75px 0;
}

/* Skills */

.skills-section {
    padding:  100px;
    margin-bottom: 100px;
}
.skills-section h2 {
    text-align: center;
    font-size: 35px;
}
.skills-section .text {
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
}
.skills-section .cells {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.skills-section .cells .cell{
    display: flex;
    justify-content: center;
    align-items: center;

    width: 200px;
    padding: 10px 20px;
    margin: 10px;
    border-radius: 1px;
}
.skills-section .cells img{
    width: 100px;
    height: 100px;
    object-fit: contain;
}

.floating-nav {
    position: fixed;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 1000; /* Ensures it stays on top */
}

.nav-item {
    padding: 10px 15px;
    color: rgb(0, 0, 0);
    text-decoration: none;
    border-radius: 8px;
    transition: background 0.3s;
    background: rgba(255, 255, 255, 0); /* Fully transparent */
}

.nav-item:hover {
    background-color: #357ABD;
}
.nav-item:hover {
    background-color: rgb(91, 202, 202); /* Light grey with transparency */
    transform: scale(1.2); /* Grows by 10%, approximately 5px */
}
